      //       angular.forEach(data, function (item) {
      //         var cId = item["cId"];

      //         arr.push(item["cId"]);
      //       })
      //       return arr;
      //     });
      // }

      // getRelatinData - для связанных таблиц, реализующих отношение "многие ко многим". Пока не использую
      // result.getRD = function (commonTab, field, id, count) {
